
































import { Component, Vue } from "vue-property-decorator";
@Component({
  components: {}
})
export default class Home extends Vue {
  username: string = "";
  active: number = 0;
  spellActive = false;
  toppings: any = [
    {
      name: "ニンニク",
      amount: 0
    },
    {
      name: "ヤサイ",
      amount: 0
    },
    {
      name: "カラメ",
      amount: 0
    },
    {
      name: "アブラ",
      amount: 0
    }
  ];

  amounts: any = [
    {
      title: "なし",
      value: 0
    },
    {
      title: "多い",
      value: 1
    },
    {
      title: "とても多い",
      value: 2
    },
    {
      title: "フードファイター",
      value: "3"
    }
  ];

  amountSpells: any = ["なし", "", "マシ", "マシマシ"];

  dialog = false;

  props = {
    source: String
  };

  private next() {
    const active = this.active;
    this.active = active < 3 ? active + 1 : 0;
  }

  get spell(): any {
    if (this.toppings[0].amount == 0) {
    }
    return `${this.toppings[0].name}${
      this.amountSpells[this.toppings[0].amount]
    }`;
  }

  private showSpell() {
    this.dialog = false;
    this.spellActive = true;
  }
}
